@use './src/common/style/global' as *;

.explorer {
  display: grid;
  grid-gap: 0;
  grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 36px 18px;
  margin: -0.35em;

  @media screen and (max-width: $bp-md-768) {
    grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
  }
}
